@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';
@import '../components/icon';

%blockquote_inside {
  position: relative;
  z-index: 1;
  margin: 0 percentage(130px / 1360px);
  padding: 0;
  font-size: 18px;
  font-weight: 500;
  line-height: 32px;
  border: 0;

  body.article & {
    margin-right: percentage(26px / 834px);
    margin-left: percentage(58px / 834px);

    @include mq_min_width($tablet-medium) {
      font-size: 25px;
      line-height: 42px;
    }
  }
}

blockquote {
  &:not(.testimonials__quote__text) {
    .article-content & {
      @extend .blockquote;
      @include clearfix;

      p {
        @extend %blockquote_inside;

        + p {
          margin-top: 1em;
        }
      }

      .icon-quote {
        display: none;
      }
    }
  }
}

.blockquote {
  position: relative;
  z-index: 0;
  max-width: none;
  margin: 30px -25px;
  padding: 60px 15px;
  color: $textcolor-1;
  background: $theme-color-1;
  overflow: hidden;

  body.node1310382 & {
    margin-right: 0;
    margin-left: 0;
  }

  @include mq_min_width($tablet-small) {
    padding-right: 30px;
    padding-left: 30px;
  }

  &::before,
  &::after { // Quotes icon background
    @include icomoon;
    position: absolute;
    z-index: 0;
    display: block;
    color: $theme-color-1-dark;
  }

  &::before { // left quote sign
    content: map_get($icon-list, 'quote-left');
    top: 20px;
    left: 20px;
    font-size: 60px;

    @include mq_min_width($tablet-small) {
      font-size: 80px;
    }

    @include mq_min_width($tablet-medium) {
      top: 40px;
      left: 40px;
      font-size: 100px;
    }
  }

  &::after { // right quote sign
    content: map_get($icon-list, 'quote-right');
    top: auto;
    right: 20px;
    bottom: 20px;
    font-size: 50px;

    @include mq_min_width($tablet-small) {
      font-size: 60px;
    }

    @include mq_min_width($tablet-medium) {
      right: percentage(160px / 1360px);
      bottom: 30px;
      font-size: 80px;
    }
  }

  .inside {
    @extend %blockquote_inside;
  }

  .block-title {
    display: none;
  }

  figure {
    position: relative;
    z-index: 1;
    margin-top: 0;
    margin-bottom: 0;

    @include mq_min_width($tablet-medium) {
      max-width: 183px;
    }
  }
}
