@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.article-wrapper {
  body.article-program & {
    position: relative;

    .article-title {
      .at-content {
        display: flex;
        flex-direction: column;

        @include mq_min_width($desktop-medium) {
          padding-top: 0;
        }

        .at-main-title {
          order: 1;
          margin: 0 0 24px;
          font-family: $font-2;
          font-size: 41px;
          font-weight: 900;
          line-height: 63px;
          text-align: center;
          color: $textcolor-3;
          border: 0 !important;
        }
      }


      p:last-of-type {
        order: 3;
        margin-bottom: 24px;
        font-size: 20px;
        font-weight: 600;
        line-height: 1.55;
        text-align: center;
        color: $lightgrey-2;
      }

      img.at-illust,
      img.gla-item-img,
      img.la-item-img,
      img.cx-lazy-load {
        width: calc(100% - 8.3334%);
        max-width: 823px;

        @include mq_min_width($tablet-medium) {
          width: calc(100% - 33.3334%);
        }
      }
    }

    .article-intro {
      display: block;

      > p {
        display: none;
      }
    }

    .article-content {
      position: relative;
      margin: 0 -8.3334% 80px;
      font-size: 16px;
      font-weight: 300;
      line-height: 1.56;
      max-width: initial;

      &::before {
        content: '';
        position: absolute;
        z-index: -1;
        top: -150px;
        width: 100%;
        height: 150px;
        background-color: $lightgrey-3;

        @include mq_min_width($desktop-small) {
          top: -100px;
          height: 100px;
        }
      }

      @include mq_min_width($tablet-medium) {
        margin-right: 0;
        margin-left: 0;
      }

      .externe.lien2 {
        color: $theme-color-2;
        font-size: 20px;
        line-height: 1.5;
      }

      .externe.lien1 {
        @extend %button;

        &::after {
          content: none;
        }
      }

      p,
      ul {
        padding: 10px 8.3334%;

        @include mq_min_width($tablet-medium) {
          padding: 10px 16.6667%;
        }
      }

      p:nth-of-type(-n+3),
      p:nth-of-type(n+6),
      ul:first-of-type {
        margin: 0;
        background-color: $lightgrey-3;
      }

      p {
        &:first-of-type {
          margin-top: 40px;
        }

        &:nth-of-type(3) {
          padding-bottom: 40px;
        }

        &:nth-of-type(4) {
          margin: 40px 0 0;
        }

        &:nth-of-type(5) {
          margin-top: 0;
        }

        &:nth-of-type(6) {
          padding-top: 40px;

          @include mq_min_width($tablet-medium) {
            padding-right: 8.3334%;
            padding-left: 30%;
          }

          @include mq_min_width(900px) {
            padding-left: 26%;
          }

          @include mq_min_width($desktop-medium) {
            padding-top: 40px !important;
            padding-left: 22%;
          }
        }

        &:nth-of-type(7) {
          a::before {
            content: '\A';
            white-space: pre;
          }
        }

        &:nth-of-type(n+6) {
          font-size: 14px;

          @include mq_min_width($tablet-medium) {
            padding: 10px 8.3334% 10px 30%;
            font-size: 20px;
          }

          @include mq_min_width(900px) {
            padding-left: 26%;
          }

          @include mq_min_width($desktop-medium) {
            padding-left: 22%;
          }
        }

        &:last-of-type {
          padding-bottom: 20px;
          font-weight: 700;

          @include mq_min_width($tablet-medium) {
            padding-bottom: 55px;
          }
        }

        a {
          text-decoration: none;
        }
      }

      ul {
        line-height: 2.312;

        li {
          margin-left: 30px;

          &::before {
            background: #000;
            border-radius: 50%;
          }
        }
      }

      iframe {
        display: block;
        max-width: 90%;
        margin: 30px auto 80px;

        @include mq_min_width($desktop-small) {
          max-width: 66.6667%;
        }
      }

      figure.fl {
        position: relative;
        margin: 0 auto;
        padding: 30px 20px 0;
        text-align: center;
        background-color: $lightgrey-3;
        border: 0;

        &::before {
          content: '';
          position: absolute;
          z-index: -1;
          top: 0;
          right: 0;
          left: 50%;
          display: block;
          width: 100vw;
          height: 100%;
          background-color: $lightgrey-3;
          transform: translateX(-50%);
        }

        @include mq_min_width($tablet-medium) {
          margin: 0;

          &::before {
            content: none;
          }
        }

        @include mq_min_width($desktop-medium) {
          padding: 50px;
        }
      }
    }

    .social-sharing {
      display: flex;
      transform: translateY(20px);
      padding: 5px;
      margin-top: 0;
      margin-bottom: 50px;

      @include mq_min_width($desktop-medium) {
        position: absolute;
        left: 0;
        top: 88px !important;
        display: block;
        border: 0;
        transform: none;
        background: none;
        width: 210px;
      }

      .inside {
        margin: auto;

        @include mq_min_width($desktop-medium) {
          margin: 0;
        }
      }

      .sr-list {
        justify-content: space-evenly;
      }

      .sr-title {
        font-size: 11px;
      }

      .sr-item {
        width: 24px;
        height: 24px;

        @include mq_min_width($desktop-medium) {
          width: 37px;
          height: 37px;
        }

        a::before {
          font-size: 37px;
        }
      }
    }

    .sr-item-txt {
      @include visually-hidden;
    }
  }

}
