@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

body.page-events {
  font-family: $font-1-alt;
  color: $textcolor-2;
  background-color: #fff;

  .block.edito.mod-catal { // header
    margin-top: 20px;

    h1 {
      margin: 0;
      padding: 0;
      font-family: $font-2;
      font-size: 26px;
      font-weight: 900;
      line-height: 45px;
      text-align: center;
      color: $textcolor-3;
      border: 0;

      @include mq_min_width($tablet-medium) {
        font-size: 42px;
        line-height: 60px;
      }
    }
  }

  .exhibitor-search-form {
    @if ($is_mobile) {
      position: relative;
      padding-bottom: 50px;

      &::before { // background
        content: '';
        position: absolute;
        z-index: -1;
        bottom: 0;
        left: 50%;
        width: 100vw;
        height: 1000px;
        background: $lightgrey-3;
        transform: translateX(-50%);
      }
    }

    .evsf-form-zone,
    .esf-form-zone {
      position: static;
      margin: 0;
      padding: 0;
      background: transparent;
      border: 0;
      border-radius: 0;

      .evsf-intro-title,
      .esf-intro-title {
        margin: 0;
        font-family: $font-1-alt;
        font-weight: 300;
        font-size: 20px;
        line-height: 31px;
        text-align: center;
        color: $textcolor-2;

        & + .evsf-search-line,
        & + .esf-search-line {
          margin-top: 34px;
        }

        label {
          font-size: inherit;
        }
      }

      .evsf-search-line {
        display: flex;
        justify-content: center;
      }

      .esf-search-line {
        position: relative;
        width: 100%;
        margin: 0 auto;
        padding-bottom: 50px;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;

        @include mq_min_width($desktop-small) {
          max-width: 95%;
          padding-bottom: 91px;
          flex-direction: row;
          justify-content: center;
        }

        @include mq_min_width($desktop-medium) {
          max-width: 77.32%;
        }

        &::before { // background
          content: '';
          position: absolute;
          z-index: -1;
          bottom: 0;
          left: 50%;
          width: 100vw;
          height: 1000px;
          background: $lightgrey-3;
          transform: translateX(-50%);
        }

        .form-select {
          width: 100%;
          max-width: 305px;
          margin: 0 0 30px;
          flex-grow: 0;
          flex-shrink: 0;
          background: #fff !important;

          @include mq_min_width($desktop-small) {
            width: auto !important;
            max-width: 100%;
            margin: 0;
            flex-grow: 1;
            flex-shrink: 1;
          }

          select {
            height: auto;
            padding: 16px 50px 16px 23px !important;
          }

          &::after {
            font-size: 11px;
            transform: rotate(-180deg) translateY(55%);
          }
        }

        #front_search_suggest_event,
        #evsf_simple_search,
        #esf_simple_search {
          margin: 0;
          width: 100%;
          max-width: 365px;
          padding: 18px 23px 16px;
          font-family: $font-1-alt;
          font-size: 16px;
          font-weight: 300;
          font-style: normal;
          line-height: 24px;
          flex-grow: 0;
          flex-shrink: 0;
          color: $textcolor-3;
          border: 0;
          border-radius: 7px;
          outline: 0;

          @include mq_min_width($desktop-small) {
            width: auto;
            max-width: 100%;
            margin: 0 30px;
            flex-grow: 1;
            flex-shrink: 1;
          }

          @include placeholder {
            color: #8C909E;
          }
        }

        #evsf-btn-search,
        #esf-btn-search {
          @extend %button;
          width: auto;
          margin: 40px 0 0;
          outline: 0;

          .visually-hidden {
            position: relative !important;
            width: auto !important;
            height: auto !important;
            clip: auto !important;
          }

          .icon-catal-search {
            display: none;
          }

          @include mq_min_width($desktop-small) {
            margin: 0;
          }
        }

        #evsf_new_search_new,
        #esf_new_search_new {
          @extend %button-underline;
          right: auto;
          bottom: auto;
          display: block;
          margin-top: 25px;

          @include mq_min_width($desktop-small) {
            position: absolute;
            right: 0;
            bottom: 51px;
            margin-top: 0;
          }

          .evsf-search-plus-txt,
          .esf-search-plus-txt {
            text-decoration: none;
          }

          &::after {
            padding: 0;
          }
        }
      }

      .evsf-advanced-search,
      .esf-advanced-search {
        margin: 30px 0 0;
        text-align: center;
        background: transparent;
        overflow: hidden;

        @include mq_min_width($desktop-small) {
          text-align: left;
        }

        .evsf-as-header {
          display: block;
        }

        .evsf-as-header button,
        .esf-as-header {
          @extend %button;
          position: relative;
          top: auto;
          height: auto;
          outline: 0;

          @if $is_mobile {
            .icon-catal & {
              display: none;
            }
          }

          .evsf-as-title,
          .esf-as-title {
            margin: 0;
            padding: 0;
            font-family: inherit;
            font-size: inherit;
            font-weight: 500;
            line-height: inherit;
            text-align: center;
            color: inherit;
            background: transparent !important;
            border: 0;
            border-radius: 0;
            transition: none;
          }

          &::before,
          &::after {
            content: none;
          }
        }

        @if not $is_mobile {
          .evsf-as-filters,
          .esf-as-filters {
            position: relative;
            top: 25px;
            margin-bottom: 25px;
            border: 1px solid #DDE1E6;

            .evsf-as-list-filters,
            .esf-as-list-filters,
            .evsf-as-filter-panel,
            .esf-as-filter-panel {
              margin-top: 0;
            }
          }
        }
      }
    }

    #evsf-results-zone,
    #esf-results-zone {
      margin-top: 30px;
      background: transparent;
      border: 0;

      .evsf-results-alpha-filter,
      .esf-results-alpha-filter {
        padding: 0;
        color: $textcolor-2;
      }
    }
  }

  .form-select {
    background: $lightgrey-5 !important;
    border: 0;
    border-radius: 7px !important;
    outline: 0 !important;

    select {
      width: 100%;
      height: 38px;
      max-width: none;
      min-height: 0;
      padding: 0 50px 0 15px !important;
      font-family: $font-1-alt;
      font-size: 16px;
      font-weight: 300;
      color: #8C909E;
      outline: 0;
      box-shadow: none !important;

      &:not([disabled]) {
        cursor: pointer;
      }

      &[disabled] {
        cursor: not-allowed;
      }

      option {
        color: $textcolor-3;
      }
    }

    &::before {
      content: none !important;
    }

    &::after {
      @include icomoon;
      content: map_get($icon-list, 'arrow-top');
      top: 50% !important;
      right: 20px;
      width: 20px;
      height: 20px;
      font-size: 8px;
      color: #9ca4bb;
      border: 0;
      transform: rotate(-180deg) translateY(72%);
    }
  }

  .evsf-ampm-list {
    position: absolute;
    width: 100%;
    margin-top: -200px;
    padding: 0;
    text-align: center;

    @if ($is_mobile) {
      justify-content: center !important;
    } @else {
      margin-left: -25px;
    }

    @include mq_min_width($desktop-small) {
      width: auto;
      margin-top: -70px;
      margin-left: 2.5%;
      text-align: left;
    }

    @include mq_min_width($desktop-medium) {
      margin-left: 11.34%;
    }

    .form-checkbox {
      @include visually-hidden;
    }
  }

  // ----------------------------------------------------------------
  .catal-results {
    width: 100%;
    margin-top: 25px !important; // Shame !
    margin-bottom: 30px;
    padding-bottom: 25px;
    align-items: center;
    border-bottom: 1px solid $lightgrey-3;

    @include mq_min_width($tablet-small) {
      display: flex;
    }
  }

  .catal-results-title {
    display: block;
    margin-right: auto;
    margin-bottom: 15px;
    padding: 0;
    font-size: 20px;
    font-weight: 300;
    line-height: 1;
    color: currentColor;
    border: 0;

    @include mq_min_width($tablet-small) {
      margin-bottom: 0;
    }
  }

  .catal-results-selectboxes {
    margin-bottom: 0;

    label {
      margin-top: 0;
      line-height: 1;
    }

    .form-item {
      display: flex;
      margin-bottom: 15px;
      flex-wrap: wrap;
      align-items: center;

      @include mq_min_width($tablet-small) {
        margin-bottom: 0;
        flex-wrap: nowrap;
      }
    }
  }

  // ----------------------------------------------------------------
  .catal-ex,
  .evl {
    display: flex;
    flex-wrap: wrap;
    margin: -15px;
    margin-top: -15px !important; // Confess !
  }

  .evl-event,
  .catal-ex-item {
    position: relative;
    display: flex;
    flex-direction: column;
    width: calc(100% - (15px * 2)); // element[width] - margins
    margin: 15px;
    padding: 0;
    background-color: #FFF;
    border: 0;
    border-radius: 5px;
    box-shadow: 0 4px 21px 0 rgba(#000, .07);
    overflow: hidden;

    @include mq_min_width($tablet-small) {
      width: calc((100% / 2) - (15px * 2));
    }

    @include mq_min_width($desktop-small) {
      width: calc((100% / 3) - (15px * 2));
    }

    a {
      color: currentColor;

      &:hover,
      &:focus,
      &:active {
        color: currentColor;
      }
    }

    .evl-event-media,
    .catal-ex-item-img {
      position: relative;
      height: 0;
      min-height: 0;
      margin: 0;
      padding-bottom: percentage(217px / 403px);
      width: 100%;
      flex-shrink: 0;
      border-bottom: 1px solid $lightgrey-3;
      overflow: hidden;

      .ezimage-field,
      .cx-lazy-load {
        position: absolute;
        top: 50%;
        left: 50%;
        max-width: none;
        max-height: none;
        transform: translate(-50%, -50%);
      }

      a:hover {
        img {
          opacity: 1;
          filter: none;
        }
      }
    }

    .evl-event-title,
    .catal-ex-item-title {
      padding-top: 30px;
      //margin-bottom: 20px;
      font-family: $font-2;
      font-size: 18px;
      font-weight: 900;
      line-height: 28px;
      color: currentColor;
      border: 0;

      @if ($is_mobile) {
        padding: 0;
      }

      a {
        text-decoration: none;
      }
    }

    .evl-event-group-title,
    .catal-ex-item-group-title {
      margin: 0 0 auto;
      padding: 0;
      flex-shrink: 0;
      border: 0;
    }

    .evl-event-buttons,
    .catal-ex-item-buttons {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      padding: 0 20px;
      margin-top: -21px; // (button[height] / 2)
      text-align: right;

      .btn-primary {
        display: inline-block;
        width: 42px;
        height: 42px;
        margin: 0;
        padding: 0;
        color: $theme-color-2;
        background-color: #FFF;
        border: 0;
        border-radius: 50%;
        box-shadow: 0 4px 10px 0 rgba(#000, .07);

        .icon-catal {
          margin: 0;
          vertical-align: inherit;
        }

        .animation-txt {
          display: none;
        }
      }
    }

    .evl-event-content,
    .catal-ex-item-content {
      position: relative;
      display: flex;
      padding: 30px 30px 15px;
      flex: 1 1 auto;
      flex-direction: column;

      .date_event {
        font-size: 16px;
        font-weight: 300;
        font-style: normal !important; // Shame
        line-height: 26px;
      }

      .evl-event-details-list,
      .organizer_event,
      .types_event,
      .topics_event {
        font-size: 13px;
        font-weight: 500;
        line-height: 1;
        color: $lightgrey-2;
      }

      .evl-event-details-list,
      .organizer_event {
        margin: 20px -30px 0;
        padding: 20px 30px 0;
        border-top: 1px solid $lightgrey-3;
      }

      .types_event {
        position: absolute;
        top: 30px;
        left: 30px;
        margin: 0;
        font-family: $font-2;
        font-size: 12px;
        font-weight: 500;
        line-height: 1;
        letter-spacing: 1.04px;
        text-transform: uppercase;
        color: currentColor;

        .evl-event-cat-title,
        .catal-ex-item-cat-title {
          display: none;
        }
      }
    }

    .evl-event-details-term,
    .evl-event-details-def,
    .catal-ex-item-cat-title {
      font-size: inherit;
      color: currentColor;
    }

    .catal-ex-item-desc {
      margin-top: 15px;
      padding: 0;
      font-size: inherit;
    }

    .evl-event-details-list,
    .evl-event-stand-item,
    .catal-ex-item-stand-item {
      margin: 0;
      padding: 0;
      font-size: inherit;
    }

    .catal-ex-item-stand,
    .icon-catal-localisation,
    .place_event,
    .description_event {
      display: none;
    }

    @if ($is_mobile) {
      .evl-event-details-term,
      .evl-event-details-def {
        float: left;
      }

      .evl-event-details-term {
        clear: left;
        font-weight: 700;
        margin-bottom: 10px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      .evl-event-details-def {
        margin-left: .3em;
      }
    }
  }

  /// ----- Mobile only
  .evsf-search-line {
    margin-top: 20px !important;
    margin-right: auto;
    margin-left: auto;

    #evsf_simple_search {
      height: auto;
      padding: 1.6rem 5rem 1.6rem 2.3rem !important;
      border: 0;
    }
  }

  .evsf-as-header {
    .evsf-btn-search {
      .icon-catal {
        display: none;
      }

      .evsf-btn-search-text {
        display: inline;
      }
    }

    .evsf-as-title {
      @extend %button-underline;
    }
  }
}

