@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

body.article .article-wrapper {
  .article-title {
    .at-tag {
      display: block;
      margin: 10px 0;
      padding: 0;
      font-family: $font-2;
      font-size: 12px;
      font-weight: 500;
      line-height: 37px;
      letter-spacing: 1.04px;
      text-align: center;
      text-transform: uppercase;
      border: 0;
    }

    .at-tag-item {
      padding: 0;
      border: 0;

      + .at-tag-item {
        margin-left: .5em;
      }
    }

    .at-tag-link {
      text-decoration: none;
      color: currentColor;

      &:hover {
        color: $theme-color-2;
      }
    }
  }

  .article-intro {
    display: block;
    max-width: 823px;
    margin: 20px auto;
    font-size: 20px;
    font-weight: 600;
    line-height: 31px;
    text-align: center;
    color: $lightgrey-2;
  }

  .article-content {
    max-width: 823px;
    margin-right: auto;
    margin-left: auto;
    font-size: 16px;

    h2 {
      margin: 12px 0;
      font-family: $font-2;
      font-size: 40px;
      font-weight: 900;
      line-height: 60px;
      color: $textcolor-3;
    }

    h3 {
      margin: 12px 0;
      font-family: $font-2;
      font-size: 25px;
      font-weight: 900;
      line-height: 40px;
      color: currentColor;
    }

    h4 {
      margin: 10px 0;
      font-family: $font-2;
      font-size: 25px;
      font-weight: 900;
      line-height: 40px;
      color: currentColor;
    }

    h5 {
      margin: 9px 0;
      font-family: $font-2;
      font-size: 18px;
      font-weight: 900;
      line-height: 26px;
      color: currentColor;
    }

    ul, ol {
      margin: 20px 0 30px;
      padding-left: 20px;

      ul, ol {
        margin: 10px 0;
        padding-left: 45px;
      }

      li {
        margin-bottom: 10px;
      }
    }

    ul {
      li {
        &::before {
          content: '';
          width: 5px;
          height: 5px;
          background-color: currentColor;
          border-radius: 50%;
        }

        li {
          &::before {
            background-color: $theme-color-2;
          }

          li {
            &::before {
              background-color: currentColor;
            }
          }
        }
      }
    }

    figure {
      float: none;
      margin: 20px 0;
      padding: 0;
      font-size: 14px;
      font-weight: 300;
      line-height: 26px;
      letter-spacing: 0;
      border: 0;
      text-align: center;

      &.fl {
        @include mq_min_width($tablet-medium) {
          float: left;
          margin-right: 30px;
          text-align: left;
        }
      }

      &.fr {
        @include mq_min_width($tablet-medium) {
          float: right;
          margin-left: 30px;
          text-align: right;
        }
      }

      img {
        display: block;
        margin: 0 auto;
      }

      figcaption {
        margin-top: 1em;
        font-size: 14px;
        color: currentColor;

        p { // text
          margin: 0;
          font-size: 14px;
          line-height: inherit;
        }

        strong { // title
          font-family: $font-2;
          font-size: 18px;
          font-weight: 900;
          line-height: 26px;
        }
      }

      small { // copyright
        display: block;
        font-size: 14px;
      }
    }

    table {
      td {
        border-color: $lightgrey-1;
      }

      tr:nth-child(odd) {
        background-color: $lightgrey-3;
      }
    }

    .lien1 {
      @extend %button-underline;
      padding-bottom: 0;
      vertical-align: bottom;
    }

    .lien2 {
      @extend %button-underline;
      padding-bottom: 0;
      vertical-align: bottom;
      color: $theme-color-2;

      &::after {
        width: 100%;
      }
    }

    .important1 {
      .lien1 {
        @extend %button;

        &::after {
          display: none;
        }
      }
    }

    .important2 {
      .lien1 {
        @extend %button;
        background: $theme-color-2;

        &::after {
          display: none;
        }
      }
    }

    .btn-primary {
      @extend %button;
    }

    .btn-secondary {
      @extend %button;
      background: $theme-color-2;
    }

    .btn-invert {
      @extend %button-invert;
    }
  }
}
