@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.home-sectors {
  clear: both;
  position: relative;
  z-index: 2;
  margin-top: 49px;
  margin-bottom: 49px;
  padding: 50px 0 60px;
  background-color: $lightgrey-3;

  @include mq_min_width($tablet-medium) {
    margin-top: 85px;
    margin-bottom: 100px;
    padding-top: 80px;
    padding-bottom: 90px;
  }

  body.article &,
  .front &,
  .node1303947 & {
    background-color: $theme-color-1;
  }

  body.page-rubric #zone2 > & {
    margin: 0 0 35px;
    background: transparent;

    @include mq_min_width($tablet-medium) {
      margin: 20px 0 70px;
      padding: 0;
    }
  }

  &::after { // overflow background
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    left: -25px;
    display: block;
    height: 100%;
    width: 100vw;
    background-color: $lightgrey-3;

    body.article &,
    .front & {
      background-color: $theme-color-1;
    }

    @include mq_min_width($tablet-medium) {
      left: 0;
      body.page-rubric #zone2 > &,
      body.article & {
        left: 50%;
        transform: translateX(-50%);
      }
      body.page-rubric #zone2 > & {
        bottom: 109px;
        top: auto;
      }
    }
  }

  &__title {
    text-align: center;
    line-height: 45px;
    margin-bottom: 20px;

    @include mq_min_width($tablet-medium) {
      line-height: 60px;
    }

    body.page-rubric #zone2 > & {
      text-align: left;
    }
  }

  &__description {
    max-width: 920px;
    margin: 0 auto 40px;
    font-family: $font-1-alt;
    font-size: 18px;
    font-weight: 300;
    line-height: 30px;
    text-align: center;
    color: $textcolor-2;

    body.page-rubric #zone2 > & {
      max-width: none;
      text-align: left;
    }

    @include mq_min_width($tablet-medium) {
      font-size: 20px;
    }
  }

  .quicklinks {
    .ql-list {
      display: flex;

      @include mq_min_width($tablet-medium) {
        margin: -10px;
        flex-wrap: wrap;
        justify-content: center;

        body.page-rubric #zone2 > & {
          justify-content: space-between;
        }
      }
    }

    .ql-item {
      position: relative;
      width: 213px;
      flex-shrink: 0;
      border-radius: 6px;
      overflow: hidden;

      @include mq_min_width($tablet-medium) {
        margin: 10px;
        overflow: visible;
      }

      body.page-rubric #zone2 > & {
        width: 232px;
      }

      &:hover {
        .btn-primary {
          &::after {
            transform: scale(1.1);
          }
        }
      }

      .btn-primary {
        position: relative;
        display: block;
        width: 100%;
        height: 185px;
        padding: 37px 23px 0;
        font-family: $font-2;
        font-size: 25px;
        font-weight: 900;
        line-height: 35px;
        text-align: left;
        text-transform: none;
        color: #fff;
        border: 0;
        border-radius: 6px;
        background: transparent;
        box-shadow: 0 4px 9px 0 rgba(#000, 0.13);
        overflow: hidden;

        .icon {
          position: relative;
          z-index: 2;
          display: block;
          height: 30px;
          margin-bottom: 20px;
          font-size: 30px;

          &.icon-produits-surgeles {
            font-size: 40px;
          }
        }

        .txt-btn {
          position: relative;
          z-index: 2;
          max-width: 160px;
          display: block;
        }

        &::before { // gradient mask shadow
          content: '';
          position: absolute;
          z-index: 1;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          background: linear-gradient(180deg, rgba(#24221d, 0) 0%, rgba(#231f1b, .66) 100%);
        }

        &::after { // background image
          content: '';
          position: absolute;
          z-index: 0;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          background: center center / cover no-repeat;
          @include transition(2s, transform);
        }
      }

      &::after { // plus corner button
        @include icomoon;
        content: map_get($icon-list, 'plus');
        position: absolute;
        z-index: 1;
        right: 0;
        bottom: 0;
        display: flex;
        width: 26px;
        height: 25px;
        font-size: 13px;
        align-items: center;
        justify-content: center;
        color: $theme-color-2;
        background: #fff;
        border-top-left-radius: 6px;
        border-bottom-right-radius: 6px;
        cursor: pointer;
      }
    }
  }

  & > .ql-item { // read-more link (moved out by JS)
    order: 99;
    width: 100%;
    margin-top: 50px;
    list-style: none;
    text-align: center;

    [class^='btn'] {
      @extend %button;
      width: auto;
      min-width: 190px;
      height: auto;
    }
  }

  /**
   * background images
   */
  $backgrounds: (
    'linkid794925': 'viandes.png',
    'linkid999999': 'viandes.png',
    'linkid794934': 'food-processing.png',
    'linkid794933': 'traiteur-snacking.png',
    'linkid794932': 'produits-surgeles.png',
    'linkid794931': 'produits-laitiers.png',
    'linkid794930': 'produits-mer.png',
    'linkid794929': 'boissons.png',
    'linkid794928': 'bio-bien-etre.png',
    'linkid794927': 'fruits-legumes.png',
    'linkid794926': 'epicerie.png',
  );

  @each $class, $image in $backgrounds {
    .ql-item.#{$class} .btn-primary:after {
      background-image: image('sectors/#{$image}');
    }
  }
}
