@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

body.page-events.page-details {
  $container-width: $desktop-medium - 30px;

  #zone2 {
    width: 100%;
  }

  #zone3,
  .catal-nav-ex {
    display: none;
  }

  .event-view-full {
    max-width: 823px;
    margin: 0 auto;
  }

  .list-articles { // header image
    position: relative;
    z-index: 1;
    margin: 25px 0 0;
    text-align: center;

    + .catal-ex-details {
      margin-top: -30px;
    }

    img {
      display: block;
      margin: 0 auto;
    }
  }

  .event-view-full {
    .block.edito.mod-catal .inside {
      position: relative;
    }

    .catal-ex-item-buttons { // floating buttons
      position: absolute !important;
      z-index: 2;
      right: 0;
      bottom: -45px;
      display: flex;

      @include mq_min_width($desktop-medium) {
        display: block;
        right: auto;
        bottom: auto;
        margin-top: 50px;
        padding-right: 15px;
        max-width: ($container-width - 823px) / 2;
        transform: translate(-100%, 100%);
      }

      .btn-primary {
        @extend %button;
        display: flex;
        width: 42px;
        height: 42px;
        line-height: 42px;
        margin-left: 0;
        margin-right: 15px;
        padding-left: 16px;
        padding-right: 16px;
        align-items: center;
        justify-content: center;
        background: #fff;
        box-shadow: 0 4px 10px 0 rgba(#000, .07);

        @include mq_min_width($desktop-medium) {
          width: 100%;
          height: auto;
          margin-right: 0;
          margin-bottom: 15px;
          line-height: 1;
          background: $textcolor-1;
          box-shadow: none;
        }

        &[data-action='add'] > .selection-empty,
        &[data-action='remove'] > .selection-full {
          display: flex;
          align-items: center;
        }

        .animation-txt {
          display: none;

          @include mq_min_width($desktop-medium) {
            display: block;
          }
        }

        .icon-catal {
          margin: 0;
          color: $theme-color-2;

          @include mq_min_width($desktop-medium) {
            order: 99;
            margin-left: 10px;
            font-size: 12px;
            transform: scale(1);
          }

          &.icon-catal-calendar {
            font-size: 16px;
          }
        }
      }
    }

    .catal-ex-item-stand-more-link {
      font-size: 14px;
      color: $theme-color-2;

      .icon-catal {
        display: none;
      }
    }

    .catal-ex-details {
      position: relative;
      z-index: 0;
      padding: 30px 0;

      &::after { // overflow background
        content: '';
        position: absolute;
        z-index: -1;
        top: 0;
        left: 50%;
        width: 100vw;
        max-width: $container-width;
        height: 100%;
        background: $lightgrey-3;
        transform: translateX(-50%);
      }

      .edito {
        margin: 0;
      }
    }

    .catal-ed-group {
      border: 0;
    }

    .catal-ed-group-title {
      padding: 0;
      border: 0;
    }

    .catal-ed-activity {
      .catal-ed-activity-group {
        padding: 0;
        border: 0;
      }

      .catal-ed-activity-list {
        line-height: 22px;
      }

      .catal-ed-activity-item {
        padding: 0;
      }
    }

    .catal-ed-activity-title {
      line-height: 22px;
    }

    .catal-ed-activity-link {
      @if ($is_mobile) {
        @extend %button;
      } @else {
        @extend %button-underline;
        line-height: 22px;
      }
    }
  }

  .catal-ed-main {
    margin: 30px 0;
    padding: 0;
    background: none;
    border: 0;
  }

  .catal-ed-main-title,
  .catal-ed-main-desc-title {
    @extend %content-h2Like;
    text-align: left;
  }

  .catal-ed-main-desc {
    margin-top: 30px;
  }

  .catal-ed-main-session {
    width: 50%;
  }

  .evd-infos-stand-item,
  .catal-ed-main-address {
    margin-top: 0;

    .icon-catal {
      margin-right: 15px;
      font-size: 22px;
      line-height: 22px;
      vertical-align: text-bottom;
      color: $theme-color-2;
    }

    span {
      position: relative;
      z-index: 0;
      display: inline-block;
      padding: 0 10px;
      font-family: $font-1;
      font-size: 14px;
      font-weight: 500;
      font-style: normal;
      line-height: 22px;
      letter-spacing: 0;
      color: #fff;

      &::after { // background
        content: '';
        position: absolute;
        z-index: -1;
        top: 50%;
        left: 50%;
        width: calc(100% + 14px);
        height: 100%;
        background: $textcolor-1;
        transform: translate(-50%, -50%) skewX(-25deg);
      }
    }
  }

  .event-link-opposite {
    padding: 0;
    font-weight: 700;
    color: $theme-color-2;
    border: 0;
  }

  /// category and thema tags
  .evd-infos-type-container {
    margin: 15px 0;
    display: flex;
    flex-direction: row-reverse;
    text-align: center;
    justify-content: center;
    align-items: center;
  }

  .evd-infos-theme,
  .evd-infos-type {
    @if ($is_mobile == false) {
      display: inline-block;
      text-align: center;
    }

    .evd-infos-type-title {
      @if ($is_mobile == false) {
        @include visually-hidden;
      }
    }

    .evd-infos-theme-list,
    .evd-infos-type-list {
      margin: 0;
      padding: 0;
      line-height: 1;
    }

    .event-type-li,
    .evd-infos-type-item,
    .evd-infos-theme-item {
      display: inline-block;
      margin: 0 15px;

      &::before {
        display: none;
      }
    }

    .event-link,
    .evd-infos-stand-link {
      position: relative;
      z-index: 0;
      display: inline-block;
      padding: 0 10px;
      font-family: $font-1;
      font-size: 16px;
      font-weight: 500;
      font-style: normal;
      line-height: 23px;
      letter-spacing: 0;
      text-decoration: none;
      color: $textcolor-2;

      &::after { // background
        content: '';
        position: absolute;
        z-index: -1;
        top: 50%;
        left: 50%;
        width: calc(100% + 14px);
        height: 100%;
        background: $theme-color-1;
        transform: translate(-50%, -50%) skewX(-25deg);
      }
    }

    &:nth-child(2) {
      .event-link {
        color: #fff;

        &::after {
          background-color: $textcolor-1;
        }
      }
    }
  }

  @if $is_mobile {
    .event-details {
      .mod-catal.edito h1 {
        @include visually-hidden;
      }

      .block.edito.mod-catal {
        display: none;
      }

      h4,
      .evd-title {
        @extend %content-h2Like;
        display: block;
        margin-top: 20px;
        border: 0;
      }
    }

    .evd-org-img {
      float: none;
      display: block;
      margin: auto;
    }

    .evd-infos-date-more-link {
      color: $theme-color-2;

      .icon-catal {
        display: none;
      }
    }

    .evd-desc {
      margin: 0;
      overflow: hidden;

      .evd-desc-title {
        @extend .main-title;
        margin-top: 0;
        padding-top: 0;
      }
    }

    .evd-group,
    .evd-infos {
      margin: 0;
      padding: 0;
      text-align: center;
      background: none;
      border: 0;
    }

    .evd-facts .evd-facts-term {
      padding-left: 0;

      &::before {
        display: none;
      }
    }

    .catal-ex-item-buttons-small {
      position: absolute;
      top: -65px;
      right: 0;
      width: 100px;
      padding: 0;
      background: none;
      border: 0;

      .btn-primary {
        height: 42px;
        width: 42px;
        margin: 0;
        padding: 0;
        color: $theme-color-2;
        background-color: #FFF;
        border: 0;
        border-radius: 50%;
        box-shadow: 0 4px 10px 0 rgba(#000, .07);

        .icon-catal {
          margin: 0;
          vertical-align: inherit;
        }
      }
    }
  }
}
