@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.list-articles.block.block-page.world-champions-tour {
  position: relative;
  display: flex;
  margin: 40px 0;
  padding: 50px 0 40px;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: flex-start;

  @include mq_min_width($tablet-medium) {
    margin: 80px 0;
  }

  @include mq_min_width($desktop-medium) {
    padding: 80px 133px 80px 0;
  }

  &::before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 50%;
    width: 100vw;
    height: 100%;
    background: $theme-color-1;
    transform: translateX(-50%);

    @include mq_min_width($desktop-medium) {
      right: 0;
      left: auto;
      transform: none;
    }
  }

  & > * {
    position: relative;
    z-index: 2;
  }

  .custom_generic_progressBar {
    @if not $is_mobile {
      display: none !important;
    } @else {
      position: relative;
      z-index: 2;
      bottom: auto;
      margin-top: 30px;
    }
  }

  .world-champions-tour-slider {
    width: 100%;
    overflow: visible;

    @include mq_min_width($desktop-medium) {
      width: 20000px;
      margin-right: -10000px;
    }
  }

  .la-slider {
    width: 100%;
    overflow: visible;

    @include mq_min_width($desktop-medium) {
      max-width: 1117px;
    }
  }

  .main-title-with-link {
    width: 100%;
    margin-bottom: 28.5px;
    padding-bottom: 15px;
    border: 0;
    border-bottom: 1px solid #fff;

    @include mq_min_width($tablet-medium) {
      width: calc(100% - 156px);
    }

    @include mq_min_width($desktop-small) {
      padding-bottom: 36.5px;
    }

    .main-title {
      float: none;
      font-size: 30px;
      font-weight: 900;
      line-height: 44px;
      text-transform: none;
      color: $textcolor-3;

      @include mq_min_width($desktop-small) {
        font-size: 40px;
        line-height: 60px;
      }
    }

    .link-view-all {
      display: none;
    }
  }

  .world-champions-tour__arrows-container {
    @include slider-arrows;
    display: none;
    margin: 0 0 28.5px;
    padding-left: 30px;
    padding-bottom: calc(15px + 10px);
    align-items: flex-end;
    border-bottom: 1px solid #fff;

    @include mq_min_width($tablet-medium) {
      display: flex;
    }

    @include mq_min_width($desktop-small) {
      padding-bottom: calc(36.5px + 10px);
    }
  }

  .slider-content {
    margin: 0;
    align-items: stretch;
    justify-content: flex-start;
    flex-wrap: nowrap;

    .la-item {
      width: calc(100vw - 50px);
      max-width: 260px;
      margin: 0;
      background: #fff;
      border-radius: 5px;
      box-shadow: 0 4px 9px 0 rgba(#000, .13);
      transition: opacity 0.2s ease-in-out;

      &:not(.swiper-slide-visible) {
        opacity: 0.4;

        & + .la-item:not(.swiper-slide-visible) {
          @include mq_min_width($desktop-medium) {
            opacity: 0.4;
          }
        }
      }

      & > a {
        position: relative;
        border-radius: 5px;

        &:hover,
        &:active,
        &:focus {
          transform: scale(1.2);
          transition: transform 7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
        }
      }

      .at-illust,
      .gla-item-img,
      .la-item-img,
      .cx-lazy-load {
        width: 100%;
        height: 131px;
        min-height: 0;
        font-family: 'object-fit: cover;';
        flex-shrink: 0;
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
        object-fit: cover;
        transition: transform 1s cubic-bezier(0.25, 0.46, 0.45, 0.94);
      }
    }

    .la-item-content {
      position: relative;
      z-index: 1;
      display: flex;
      height: calc(100% - 131px);
      padding: 22px 25px 20px;
      text-align: left;
      flex-direction: column;
      justify-content: flex-end;
      align-items: flex-start;
      border-bottom-left-radius: 6px;
      border-bottom-right-radius: 6px;

      & > * {
        position: relative;
        z-index: 1;
      }

      .la-item-tag {
        position: relative;
        top: auto;
        left: auto;
        display: block;
        width: 100%;
        min-height: 32px;
        margin-bottom: 14px;
        padding: 0 55px 0 0;
        font-size: 12px;
        font-family: $font-2;
        font-weight: 500;
        line-height: 22px;
        letter-spacing: 1.04px;
        text-transform: none;
        color: $textcolor-2;
        background: none;
      }

      .la-item-title {
        position: static;
        margin-bottom: 8px;
        font-family: $font-2;
        font-size: 18px;
        font-weight: 900;
        line-height: 26px;
        letter-spacing: 1.04px;
        color: $textcolor-2;

        & > a {
          color: $textcolor-2;
        }

        span[class^='category-prix'] {
          display: none;
        }

        [class^='award-'] {
          content: '';
          position: absolute;
          top: 10px;
          right: 11px;
          width: 61px;
          height: 45px;
          text-align: right;
          background: center / cover no-repeat;
        }

        @each $award, $image in $inno-awards {
          .award-#{$award} {
            background-image: image('visual/#{$image}');
          }
        }
      }

      .la-item-desc {
        margin-top: auto;
        font-family: $font-1;
        font-size: 15px;
        font-weight: 500;
        line-height: 27px;
        color: $lightgrey-2;

        strong:not(:first-of-type) {
          &::before {
            content: '\A';
            white-space: pre;
          }
        }
      }

      .la-item-btn {
        margin: 7px 0 0;

        .btn-primary {
          @extend %button-underline;
          font-size: 0;
          letter-spacing: 0;
          color: transparent;
          cursor: pointer;

          .icon {
            display: none;
          }

          &::before {
            font-size: 16px;
            letter-spacing: normal;
            color: $textcolor-5;

            html[lang='fr'] & {
              content: 'Voir plus';
            }

            html[lang='en'] & {
              content: 'See more';
            }
          }

          &:hover,
          &:active {
            &::after {
              width: 100%;
            }
          }
        }
      }
    }
  }

  .include_in_above_block {
    @extend %button;
    margin-top: 40px;
    margin-right: auto;
    margin-left: auto;

    @include mq_min_width($tablet-medium) {
      margin-right: 0;
      margin-left: 0;
    }

    @include mq_min_width($desktop-medium) {
      margin-top: 50px;
    }
  }
}
