body.faq {
  .block-page.faq {
    position: relative;
    padding: 20px 0 50px;

    @include mq_min_width($tablet-medium) {
      padding: 20px 15px 50px;
    }

    @include mq_min_width($desktop-small) {
      padding: 20px 106.5px 92px;
    }

    &::before { // overflow background
      content: '';
      position: absolute;
      z-index: -1;
      top: -80px;
      left: 50%;
      width: 100vw;
      height: calc(100% + 80px);
      background-color: $lightgrey-3;
      transform: translateX(-50%);
    }

    .faq-main-title {
      margin-bottom: 56px;
      padding: 0;
      font-family: $font-2;
      font-weight: 900;
      font-size: 30px;
      line-height: 45px;
      text-align: center;
      color: $textcolor-3;
      border: 0;

      @include mq_min_width($tablet-medium) {
        font-size: 42px;
        line-height: 60px;
      }
    }

    .fc-item-nb,
    .faq-summary,
    .faq-sub-title,
    .intro {
      display: none;
    }

    .fc-title {
      margin-bottom: 1.5rem;
      padding: 0;
      font-family: $font-2;
      font-size: 2.5rem;
      font-weight: 900;
      color: $textcolor-2;
      border: 0;
    }

    .faq-content {
      padding: 0;
    }

    .fc-item-link {
      position: relative;
      display: block;
      padding: 30px 90px 30px 20px;
      font-size: 20px;
      font-weight: 600;
      line-height: 31px;
      text-decoration: none;
      color: $textcolor-2;

      @include mq_min_width($tablet-medium) {
        padding-right: 100px;
        padding-left: 30px;
      }

      @include mq_min_width($desktop-small) {
        padding-right: 179px;
        padding-left: 109px;
      }

      .icon {
        display: none;
      }

      &::before,
      &::after {
        position: absolute;
        top: 22.5px;
        right: 20px;
        width: 41px;
        height: 41px;
        box-sizing: border-box;

        @include mq_min_width($tablet-medium) {
          right: 30px;
        }

        @include mq_min_width($desktop-small) {
          right: 109px;
        }
      }

      &::before {
        content: '';
        border: 1px solid $theme-color-2;
        border-radius: 50%;
        transition: background-color 0.2s ease-in-out;
      }

      &::after {
        @include icomoon;
        content: map_get($icon-list, 'plus');
        display: flex;
        font-size: 15px;
        align-items: center;
        justify-content: center;
        color: $theme-color-2;
        transition: color 0.2s ease-in-out;
      }

      &:hover,
      &:active {
        &::before {
          background: $theme-color-2;
        }

        &::after {
          color: #fff;
        }
      }
    }

    .fc-item {
      position: relative;
      padding: 0;
      background-color: #fff;
      border: 0;
      transition: box-shadow 0.2s ease-in-out;
      box-shadow: 0 4px 9px 0 rgba(#000, 0);

      &.is-open {
        box-shadow: 0 4px 9px 0 rgba(#000, .13);

        .fc-item-link {
          &::after {
            content: map_get($icon-list, 'minus');
          }
        }
      }

      &:not(:last-of-type) {
        margin-bottom: 5px;
      }
    }

    .fc-item-content {
      padding: 30px 20px 19px;
      line-height: 31px;
      font-size: 20px;
      font-weight: 300;
      background-color: #FDF8D4;

      @include mq_min_width($tablet-medium) {
        padding: 30px 30px 19px;
      }

      @include mq_min_width($desktop-small) {
        padding: 30px 109px 19px;
      }
    }
  }
}
