body.speakers-sial,
body.node1318742 {

  .speaker-sial > .block-title {
    @extend .main-title;
    position: relative;
    padding: 0 10px 80px;
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-bottom: 15px;

    &::before {
      content: '';
      position: absolute;
      z-index: -1;
      top: 0;
      left: 50%;
      display: block;
      height: 200%;
      width: 110vw;
      background-color: $lightgrey-3;
      transform: translate(-50%, -50%);
    }

    > * {
      color: $textcolor-2;
      font-size: 20px;
      font-weight: 300;
      line-height: 1.5;
      text-align: center;

      @include mq_min_width($desktop-small) {
        padding: 0 25%;
      }
    }
  }

  .contacts-list {
    padding: 30px 0;
    position: relative;
    

    h1 {
      @extend .main-title;
      margin-bottom: 12px;
      text-align: center;
    }

    .intro {
      position: relative;
      padding-bottom: 80px;
      margin-bottom: 15px;
      text-align: center;

      &::before {
        content: '';
        position: absolute;
        z-index: -1;
        top: -80px;
        left: 50%;
        display: block;
        height: 200%;
        min-height: 381px;
        width: 110vw;
        background-color: $lightgrey-3;
        transform: translate(-50%, -50%);
      }

      & + h2 {
        display: none;
      }
    }

    .cl-content {
      @extend %commercial-card-wrapper;
      margin-top: 50px;

      @include mq_min_width(640px) {
        margin-left: -15px;
        margin-right: -15px;
      }
    }

    .cl-item {
      @extend %commercial-card;
      flex-direction: column;
      margin: 15px auto;
      width: calc((100% / 2) - (15px * 2));

      @include mq_min_width(640px) {
        margin-left: 14px;
        margin-right: 14px;
      }

      @include mq_min_width($tablet-medium) {
        width: calc((100% / 2) - (15px * 2));
      }

      @include mq_min_width($desktop-small) {
        width: calc((100% / 3) - (15px * 2));
      }      

      @include mq_min_width($desktop-medium) {
        width: 100%;
        max-width: 291px;
      }

      &.is__hidden {
        display: none;
      }

      &-photo {
        height: 212px;
        overflow: hidden;

        > img {
          width: 100%;
          border-top-left-radius: 5px;
          border-top-right-radius: 5px;
        }
      }

      &-content {
        padding: 25px 30px 15px;
        display: flex;
        flex-direction: column;
        align-items: stretch;
        min-height: 138px;
        background-color: #fff;
        border-bottom-right-radius: 5px;
        border-bottom-left-radius: 5px;
      }

      &-title {
        @extend %commercial-card-title;
        font-size: 18px;
        line-height: 1.55;
        order: 2;
        margin-bottom: 5px;
      }

      &-subtitle {
        order: 3;
        font-family: $font-1;
        font-weight: 300;
        font-size: 16px;
        line-height: 1.625;
        text-transform: lowercase;

        &:first-letter {
          text-transform: capitalize;
        }
      }

      &-email,
      &-phone,
      &-illust,
      &-social.additional-informations {
        display: none;
      }

      &-social {
        @extend %commercial-card-social;
        margin-bottom: 11px;
      }
    }
  }

  .mod-catal {
    position: relative;

    &.edito,
    &.events {
      &::before {
        content: '';
        position: absolute;
        z-index: -1;
        top: 0;
        left: 50%;
        display: block;
        height: 100%;
        width: 100vw;
        background-color: $theme-color-1;
        transform: translateX(-50%);
      }
    }
  }

  .edito.mod-catal {
    .main-title-with-link {
      display: flex;
      flex-direction: column;
      border: 0;
      padding-top: 70px;
      margin-bottom: 0;

      @include mq_min_width(992px) {
        flex-direction: row;
      }

      h2 {
        @extend .main-title;
        margin-left: 0;
      }

      a {
        @extend %button;
        margin-top: 15px;
        margin-left: 0;

        @include mq_min_width(992px) {        
          margin: auto 0 auto auto;
        }

        .icon {
          display: none;
        }
      }
    }
  }

  .catal-ex {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 70px;
    padding-top: 40px;
    margin: -15px -15px 72px;
  }

  .catal-ex-item {
    position: relative;
    display: flex;
    flex-direction: column;
    width: calc(100% - (15px * 2));
    margin: 15px;
    padding: 0;
    background-color: #FFF;
    border: 0;
    border-radius: 5px;
    box-shadow: 0 4px 21px 0 rgba(#000, .07);
    overflow: hidden;

    @include mq_min_width($tablet-medium) {
      width: calc((100% / 2) - (15px * 2));
    }

    @include mq_min_width($desktop-small) {
      width: calc((100% / 3) - (15px * 2));
    }

    a {
      color: currentColor;

      &:hover,
      &:focus,
      &:active {
        color: currentColor;
      }
    }

    .catal-ex-item-img {
      position: relative;
      height: 0;
      min-height: 0;
      margin: 0;
      padding-bottom: percentage(217px / 403px);
      width: 100%;
      flex-shrink: 0;
      border-bottom: 1px solid $lightgrey-3;
      overflow: hidden;

      .ezimage-field,
      .cx-lazy-load {
        position: absolute;
        top: 50%;
        left: 50%;
        max-width: none;
        max-height: none;
        transform: translate(-50%, -50%);
      }

      a:hover {
        img {
          opacity: 1;
          filter: none;
        }
      }
    }

    .catal-ex-item-title {
      padding-top: 30px;
      margin-bottom: 20px;
      font-family: $font-2;
      font-size: 18px;
      font-weight: 900;
      line-height: 28px;
      color: currentColor;
    }

    .catal-ex-item-group-title {
      margin: 0;
      padding: 0;
      flex-shrink: 0;
      border: 0;
    }

    .catal-ex-item-buttons {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      padding: 0 20px;
      margin-top: -21px; // (button[height] / 2)
      text-align: right;

      .btn-primary {
        height: 42px;
        width: 42px;
        margin: 0;
        padding: 0;
        color: $theme-color-2;
        background-color: #FFF;
        border: 0;
        border-radius: 50%;
        box-shadow: 0 4px 10px 0 rgba(#000, .07);

        .icon-catal {
          margin: 0;
        }

        .animation-txt {
          display: none;
        }
      }
    }

    .catal-ex-item-content {
      position: relative;
      display: flex;
      flex-direction: column;
      padding: 30px 30px 15px;

      .date_event {
        font-size: 16px;
        font-weight: 300;
        font-style: normal !important; // Shame
        line-height: 26px;
      }

      .organizer_event,
      .types_event,
      .topics_event {
        font-size: 13px;
        font-weight: 500;
        line-height: 2.84;
        color: $lightgrey-2;
      }

      .organizer_event {
        margin: 20px -30px 0;
        padding: 20px 30px 0;
        border-top: 1px solid $lightgrey-3;
      }

      .types_event {
        position: absolute;
        top: 30px;
        left: 30px;
        margin: 0;
        font-family: $font-2;
        font-size: 12px;
        font-weight: 500;
        line-height: 1;
        letter-spacing: 1.04px;
        text-transform: uppercase;
        color: currentColor;

        .catal-ex-item-cat-title {
          display: none;
        }
      }
    }

    .catal-ex-item-cat-title {
      font-size: inherit;
      color: currentColor;
    }

    .catal-ex-item-desc {
      margin-top: 15px;
      padding: 0;
      font-size: inherit;
    }

    .catal-ex-item-stand-item {
      margin: 0;
      padding: 0;
      font-size: inherit;
    }

    .catal-ex-item-stand-more {
      display: none;
    }

    .icon-catal-localisation,
    .place_event,
    .description_event {
      display: none;
    }
  }
}