.detail-secteur {
  display: flex;
  padding: 50px 0;
  font-family: $font-1-alt;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;

  @include mq_min_width($desktop-small) {
    @include clearfix;
    display: block;
    margin: 28px 0;
    padding: 72px 106.5px;
  }

  &:nth-last-child(1) {
    margin-bottom: 0;
  }

  & + *:not(.detail-secteur) {
    margin-top: 50px;
  }

  &__main {
    order: 1;
    width: 100%;
    font-size: 16px;
    line-height: 25px;

    @include mq_min_width($desktop-small) {
      width: 100%;
    }

    h2 {
      margin-bottom: 25px;
      padding: 0;
      font-family: $font-2;
      font-size: 30px;
      font-weight: 900;
      line-height: 44px;
      color: $textcolor-3;
      border: 0;

      @include mq_min_width($desktop-small) {
        font-size: 40px;
        line-height: 60px;
      }
    }

    p {
      text-align: justify;
    }

    p, .p-like, ul, ol, dl, blockquote, pre, td, th, label, textarea, caption, details, figure {
      &:not(:nth-child(1)) {
        margin-top: 25px;
      }
    }
  }

  &__additionnal {
    order: 2;
    width: 100%;
    margin-top: 30px;

    @include mq_min_width($desktop-small) {
      width: 45.37%;

      &.align-right {
        float: right;
        margin: 0 0 30px 60px;
      }

      &.align-left {
        float: left;
        margin: 0 60px 30px 0;
      }
    }

    & > ul.detail-secteur__datas + img,
    & > img + ul.detail-secteur__datas {
      margin-top: 30px;
    }

    & > img {
      display: block;
      margin-right: auto;
      margin-left: auto;
    }
  }

  ul.detail-secteur__datas {
    margin: 0;
    padding: 15px 25px;
    color: $textcolor-4;
    background: $lightgrey-3;

    @include mq_min_width($tablet-small) {
      padding: 22px 50px 24px;
    }

    & > li {
      margin-bottom: 0;
      padding-top: 16px;
      padding-bottom: 20px;

      @include mq_min_width($tablet-small) {
        display: flex;
        align-items: stretch;
        justify-content: space-between;
      }

      p {
        display: flex;
        font-family: $font-2;
        font-weight: 900;
        font-size: 20px;
        line-height: 32px;
        flex-grow: 1;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;

        .nth-line {
          display: block;
          margin-top: 5px;
          font-family: $font-1-alt;
          font-size: 18px;
          font-weight: 400;
          line-height: 23px;

          .line {
            position: relative;
            z-index: 1;
            display: inline-block;

            &::before {
              content: '';
              position: absolute;
              z-index: -1;
              top: 0;
              left: -7.5px;
              width: calc(100% + 15px);
              height: 100%;
              background: $theme-color-1;
              transform: skewX(-25deg);
            }

            &:not(:last-of-type) {
              margin-bottom: 10px;
            }
          }
        }
      }

      .icon {
        display: flex;
        width: 63px;
        height: auto;
        margin: 0 0 10px;
        font-size: 45px;
        flex-shrink: 0;
        align-items: flex-start;
        justify-content: flex-start;
        color: $lightgrey-1;
        background: none;
        transform: none;

        @include mq_min_width($tablet-small) {
          margin: 0 25px 0 0;
          padding-top: 9px;
        }
      }

      &::before { // list-item dot
        display: none;
      }

      &:not(:last-of-type) {
        position: relative;
        padding-bottom: 22px;

        &::after { // list-item divider
          content: '';
          position: absolute;
          bottom: 0;
          left: -25px;
          width: 100%;
          height: 2px;
          background: $lightgrey-1;

          @include mq_min_width($tablet-small) {
            left: 0;
            width: 73%;
          }
        }
      }
    }
  }

  &.blue-mode {
    position: relative;
    z-index: 1;
    background-color: $lightgrey-3;

    &::before { // blue background
      content: '';
      position: absolute;
      z-index: -1;
      top: 0;
      left: 50%;
      width: 1361px;
      height: 100%;
      background-color: $lightgrey-3;
      transform: translateX(-50%);
    }

    .detail-secteur__datas {
      background-color: #fff;

      & > li {
        &:not(:last-of-type) {
          &::after { // list-item divider
            background: $theme-color-1;
          }
        }
      }
    }
  }
}
