.local-agent {

  &__linkTo {
    @extend %button;
    margin-right: 20px;
  }

  &__filters {
    display: flex;
    align-items: center;

    select {
      border-radius: 7px;
      background-color: rgba($lightgrey-5, 0.6);
      margin-left: 16px;
      padding: 12px 22px 10px;
      width: 231px;
      border: 0;
      appearance: none;
    }
  }

  &__select {
    position: relative;

    &::after {
      @include icomoon;
      content: '\1f51d';
      width: 2rem;
      height: 2rem;
      position: absolute;
      top: 24px;
      right: 25px;
      transform: rotate(-180deg) translateY(55%);
      color: #9ca4bb;
      border: 0;
      pointer-events: none;
    }
  }

  &__wrap {
    display: flex;
    flex-direction: column;
  }

  .more-infos-contact__jobTitle {
    color: $textcolor-2;
    font-family: $font-1;
    font-weight: 500;
    line-height:1.56;
  }

  .more-infos-contact__image {
    display: none;
  }

.more-infos-contact__inner {
  padding-bottom: 68px;
}
}
