@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.form-txt {
  font-family: $font-1-alt;
  font-size: 16px;
  font-weight: 300;
  line-height: 29px;
  color: $textcolor-1;

  @include placeholder {
    font-size: inherit !important;
    font-weight: inherit !important;
    font-style: inherit !important;
    color: $textcolor-2 !important;
  }
}


.form-item-radio,
.form-item-checkbox {
  .radio-title::after,
  .checkbox-title::after {
    .mod-catal & {
      background-color: transparent;
      border-width: 1px;
      border-color: $textcolor-3;
      border-radius: 2px;
    }
  }

  input[type='radio']:checked,
  input[type='checkbox']:checked {
    + .radio-title::after,
    + .checkbox-title::after {
      .mod-catal & {
        background-color: $theme-color-2;
        border-color: $textcolor-3;
      }
    }
  }
}
