@each $name, $glyph in $icon-list {
  .icon.icon-#{$name}::before {
    @include icomoon;
    content: $glyph;
  }
}

/**
 * Other overrides
 */
.cap-icon-shopping_basket::before {
  @include icomoon;
  content: map_get($icon-list, 'star') !important;
}

.icon-apps {
  &::before {
    @include icomoon;
    content: map_get($icon-list, 'apple');
  }

  &::after {
    @include icomoon;
    margin-left: .66em;
    content: map_get($icon-list, 'android');
  }
}

.icon-long-arrow-right::before,
.icon-catal-arrow-right::before,
.icon-catal-long-arrow-right::before {
  @include icomoon;
  content: map_get($icon-list, 'arrow-top');
  display: block;
  transform: rotate(90deg);

  .mod-catal & {
    content: map_get($icon-list, 'arrow-top');
  }
}

.icon-long-arrow-left::before,
.icon-catal-arrow-left::before,
.icon-catal-long-arrow-left::before {
  @include icomoon;
  content: map_get($icon-list, 'arrow-top');
  display: block;
  transform: rotate(-90deg);

  .mod-catal & {
    content: map_get($icon-list, 'arrow-top');
  }
}

