@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';
@import '../components/icon';

.contacts-list {
  // Page /Infos-Pratiques/Equipe-commerciale
  body.node1309141 & {
    h1 {
      display: none;
    }

    h2 {
      font-family: $font-2;
      font-size: 25px;
      font-weight: 900;
      line-height: 1.6;
      color: $textcolor-2;
      border-bottom: 1px solid rgba(0, 0, 0, 0.15);
      margin-bottom: 25px;
    }

    .intro {
      position: relative;
      padding: 0 10px 64px;
      margin-bottom: 60px;
      order: 1;

      &::after {
        content: '';
        position: absolute;
        z-index: -1;
        top: 0;
        left: 50%;
        display: block;
        width: 110vw;
        height: 190%;
        background-color: $lightgrey-3;
        transform: translate(-50%, -50%);
      }

      h3 {
        @extend .main-title;
      }

      p {
        font-size: 20px;
        font-weight: 300;
        line-height: 1.5;
        text-align: center;
        color: $textcolor-2;

        @include mq_min_width($desktop-small) {
          padding: 0 25%;
        }
      }
    }

    .inside {
      display: flex;
      flex-direction: column;

      > h2:first-of-type,
      > .cl-content:not(:last-of-type) {
        position: relative;

        &::before {
          content: '';
          display: block;
          position: absolute;
          z-index: -1;
          top: 0;
          right: 0;
          left: 50%;
          width: 100vw;
          height: 100%;
          background-color: $lightgrey-3;
          transform: translateX(-50%);

          @include mq_min_width($desktop-medium) {
            left: -6.3rem;
            transform: initial;
          }
        }
      }

      > h2 {
        order: 3;

        &:not(:first-of-type) {
          display: none;
        }

        &:first-of-type {
          @extend .main-title;
          margin-bottom: 0;
          margin-left: 0;
          padding-top: 51px;

          + .cl-content {
            margin-top: 0;
          }

          &::before {
            @include mq_min_width($desktop-medium) {
              left: -7.8rem;
            }
          }
        }
      }

      > .cl-content {
        &:not(:last-of-type) {
          order: 4;
          padding: 41px 0 51px;

          .cl-item {
            @include mq_min_width($tablet-medium) {
              width: calc((100% / 3) - 30px);
            }

            @include mq_min_width($desktop-medium) {
              width: calc((100% / 4) - 30px);
              max-width: 291px;
            }

            &-subtitle,
            &-address {
              display: none !important;
            }

            &-email,
            &-phone {
              display: none;
            }

            &-content {
              min-height: 138px;
            }
          }
        }

        &:last-of-type {
          order: 2;
          margin-bottom: 80px;

          .cl-item {
            @include mq_min_width($tablet-medium) {
              width: calc((100% / 3) - 30px);
              margin-top: 30px;
              margin-bottom: 0;
            }

            @include mq_min_width($desktop-small) {
              width: calc((100% / 2) - 30px);
              margin-top: 90px;
              flex-direction: initial;

              &:nth-of-type(-n+2) {
                margin-top: 50px;
              }
            }

            &-photo {
              @include mq_min_width($desktop-small) {
                width: 212px;
                border-radius: 5px 0 0 5px;
              }
            }

            &-content {
              @include mq_min_width($desktop-small) {
                position: relative;
                width: calc(100% - 212px);
                max-height: 212px;
                margin-top: 0;
                margin-left: auto;
                padding-top: 30px;
              }
            }

            &-subtitle {
              order: 3;
              margin-bottom: 11px;
              line-height: 1.6;
              text-transform: capitalize;

              @include mq_min_width($desktop-small) {
                font-size: 25px;
                transform: translate3d(-240px, -80px, 0);
              }
            }

            &-subtitle,
            &-address {
              @include mq_min_width($desktop-small) {
                position: absolute;
                top: 0;
                width: 150%;
              }
            }

            &-phone,
            &-email {
              order: 4;
              display: flex;
              width: 100%;
              margin-top: 5px;
              color: $textcolor-2;

              @include mq_min_width($desktop-small) {
                align-items: center;
              }

              .label {
                display: none;
              }

              a {
                @include text-ellipsis;
                text-decoration: none;
              }
            }

            &-phone .icon-phone,
            &-email .icon-email {
              display: flex;
              margin-top: 5px;
              margin-right: 11px;

              @include mq_min_width($desktop-small) {
                margin-top: initial;
              }

              &::before {
                @include icomoon;
                font-size: 15px;
              }
            }

            &-phone {
              & + .cl-item-phone {
                margin-top: 0;

                .icon-phone {
                  opacity: 0;
                }
              }

              .icon-phone {
                &::before {
                  content: map_get($icon-list, 'phone');
                }
              }
            }

            &-email {
              .icon-email {
                &::before {
                  content: map_get($icon-list, 'email');
                }
              }
            }
          }
        }
      }
    }

    .cl-content {
      @extend %commercial-card-wrapper;

      @include mq_min_width(660px) {
        margin: 20px -15px;
      }
    }

    .break {
      flex-basis: 100%;
      height: 0;
    }

    .cl-item {
      @extend %commercial-card;
      width: 291px;
      margin: 15px auto;
      flex-direction: column;

      @include mq_min_width(640px) {
        width: calc((100% / 2) - 30px);
        margin-right: 15px;
        margin-left: 15px;
      }

      &.has__firstJobTitle,
      &.has__borderTop {
        position: relative;
        margin-top: 80px;

        @include mq_min_width($desktop-medium) {
          &::after {
            position: absolute;
            content: '';
            display: block;
            top: -25px;
            height: 2px;
            background-color: rgba(#000, 0.15);
          }
        }
      }

      &.has__borderTop {
        @include mq_min_width($desktop-medium) {
          &::after {
            left: calc(-10% - 1px);
            width: calc(110% + 1px);
          }
        }
      }

      &.has__firstJobTitle {
        &::before {
          position: absolute;
          top: -65px;
          left: 0;
          width: 100%;
          content: attr(data-job-title);
          color: $textcolor-4;
          font-family: $font-2;
          font-size: 15px;
          font-weight: 500;
          letter-spacing: 1.3px;
          line-height: 2.46;
          text-transform: uppercase;
        }

        &::after {
          width: 100%;
          left: 0;
        }
      }

      &-illust {
        display: none;
      }

      &-photo {
        position: relative;
        height: 212px;
        overflow: hidden;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;

        > img {
          width: 100%;
          position: absolute;
          top: 0;
          left: 50%;
          max-width: none;
          max-height: none;
          transform: translate(-50%, 0);
        }
      }

      &-content {
        @extend %commercial-card-content;
        min-height: 212px;
        margin-top: 10px;
        padding: 20px 18px 26px 30px;
      }

      &-address {
        color: $textcolor-2;

        @include mq_min_width($desktop-small) {
          transform: translate3d(-240px, -35px, 0);
        }
      }

      &-title,
      &-subtitle {
        @extend %commercial-card-title;
      }

      &-title {
        @include text-ellipsis;
        order: 2;
        margin-top: 11px;
        font-size: 18px;
        line-height: 1.55;

        @include mq_min_width($desktop-small) {
          margin: 11px 0;
        }
      }

      &-social {
        @extend %commercial-card-social;
        order: 1;

        &.additional-informations {
          display: none;
        }
      }
    }
  }
}

body.node1309141 .quicklinks-cta--large {
  display: none;
}
