body.faq.article_list {
  #zone4 .more-infos-contact {
    display: none;
  }

  .block-page.list-articles {
    margin-bottom: 40px;
    padding: 20px 0 30px;

    @include mq_min_width($tablet-medium) {
      margin-bottom: 80px;
      padding: 20px 0 50px;
    }

    &::before {
      top: -80px;
      height: calc(100% + 80px);
      max-height: none;
    }

    .intro {
      display: none;
    }

    .grid-la-list {
      margin: 20px 0 0;

      .gla-item {
        width: 100%;
        margin-bottom: 30px;
        margin-left: 0;
        border-radius: 0;
        box-shadow: none;

        @include mq_min_width($tablet-small) {
          display: flex;
          align-items: stretch;
          justify-content: space-between;
        }

        @include mq_min_width(881px) {
          width: calc((100% - 1 * 30px) / 2);
          margin-left: 30px;

          &:nth-child(2n + 1) {
            margin-left: 0;
          }
        }

        @include mq_min_width(1141px) {
          width: calc((100% - 2 * 30px) / 3);

          &:nth-child(2n + 1) {
            margin-left: 30px;
          }

          &:nth-child(3n + 1) {
            margin-left: 0;
          }
        }

        & > a {
          position: relative;
          height: auto;
          border-radius: 0;

          @include mq_min_width($tablet-small) {
            width: 29.80%;
          }

          img {
            width: 100%;
            min-height: 0;
            max-height: 329px;
            border-radius: 0;

            @include mq_min_width($tablet-small) {
              position: absolute;
              top: 50%;
              left: 50%;
              height: 100%;
              max-height: none;
              transform: translate(-50%, -50%);
              transition: transform 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
            }
          }
        }

        &:hover,
        &:active,
        &:focus {
          & > a {
            img {
              @include mq_min_width($tablet-small) {
                transform: translate(-50%, -50%) scale(1.2);
              }
            }
          }
        }

        .gla-item-content {
          right: auto;
          bottom: auto;
          left: auto;
          display: block;
          height: auto;
          padding: 35px 46px 40px 29px;
          text-align: center;
          border-radius: 0;

          @include mq_min_width($tablet-small) {
            width: 70.2%;
            text-align: left;
          }

          & > a {
            display: block;
            text-align: left;
            text-decoration: none;

            &.lien2 {
              position: relative;
              padding: 2px 0;
              font-weight: 600;
              line-height: 26px;

              &:not(:nth-last-child(1)) {
                margin-bottom: 10px;
              }

              &::before {
                content: '';
                position: absolute;
                bottom: 0;
                left: 0;
                width: 34px;
                height: 0.76px;
                background: $theme-color-2;
              }
            }

            &.lien3 {
              @extend %button;
              margin-top: 30px;
            }

            &:nth-child(1):not(.lien2):not(.lien3) {
              & + .lien3 {
                margin-top: 0;
              }
            }
          }
        }

        .gla-item-desc,
        .gla-item-btn {
          display: none;
        }

        .gla-item-title {
          margin-bottom: 20px;
          font-family: $font-2;
          font-size: 25px;
          font-weight: 900;
          line-height: 40px;
          color: $textcolor-2;
        }
      }
    }
  }
}
