.pagination {
  display: flex;
  width: 100%;
  margin: 40px 0;
  padding: 0;
  line-height: 26px;
  align-items: center;
  justify-content: center;
  border: 0;

  @include mq_min_width($tablet-medium) {
    margin: 50px 0 60px;
  }
  @include mq_min_width($desktop-small) {
    margin: 60px 0 80px;
  }
  @include mq_min_width($desktop-medium) {
    margin: 70px 0 100px;
  }

  .pagin-item {
    margin: 0;

    &:not(:first-of-type) {
      margin-left: 5px;
    }

    &:not(:last-of-type) {
      margin-right: 5px;
    }

    a {
      display: block;
      padding: 0;
      text-decoration: none;
      color: $lightgrey-2;
      background: transparent !important;
      transition: color 0.3s ease-in-out;
    }

    &:hover,
    &:active,
    &.is-active {
      a {
        color: $theme-color-2;
      }
    }

    &.is-active {
      font-weight: 600;
    }

    &.pagin-prev,
    &.pagin-next {
      a {
        font-size: 0;
        letter-spacing: 0;
        line-height: 0;

        .icon,
        .icon-catal {
          display: flex;
          margin: 0;
          font-size: 12px;
          line-height: 26px;
          letter-spacing: normal;
          align-items: center;
          justify-content: center;
          color: currentColor;
        }
      }
    }

    &.pagin-prev {
      margin-right: 20px;
    }

    &.pagin-next {
      margin-left: 20px;
    }
  }
}
