.article-navigation {
  body.article & {
    max-width: 850px;
    margin: 30px auto;
  }

  body.article-program & {
    position: relative;
    margin-bottom: 80px;
    padding: 80px 0;

    &::before { // overflow background
      content: '';
      position: absolute;
      z-index: -1;
      top: 0;
      right: 0;
      left: 50%;
      display: block;
      width: 100vw;
      height: 100%;
      background-color: $lightgrey-3;
      transform: translateX(-50%);
    }
  }

  body.detail-actu & {
    margin-bottom: 80px;
  }

  .an-list {
    @include clearfix;

    body.detail-actu &,
    body.article-program & {
      display: flex;
      align-items: flex-start;
      flex-wrap: wrap;
      flex-direction: column;

      @include mq_min_width($tablet-medium) {
        flex-direction: row;
      }
    }
  }

  .an-item-content {
    body.detail-actu &,
    body.article-program & {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-end;
      width: 100%;
      height: 100%;
      padding: 197px 15px 43px;
      background: linear-gradient(180deg, rgba(36, 34, 29, 0) 0%, rgba(35, 31, 27, 0.66) 100%);
      z-index: 2;
      color: #fff;

      @include mq_min_width($desktop-small) {
        min-height: 435px;
      }

      @include mq_min_width($desktop-medium) {
        padding: 197px 30px 43px;
      }

      .at-theme {
        margin-left: 15px !important;
      }

      .an-item-publication,
      .icon-short-arrow-right {
        display: none;
      }

      .an-item-title {
        color: #fff;
        font-family: $font-2;
        font-size: 24px;
        font-weight: 900;
        line-height: 32px;
        margin-top: 20px;

        & + .gla-item-desc {
          margin-top: 10px;
        }
      }
    }
  }

  .an-item {
    body.detail-actu &,
    body.article-program & {
      margin: 15px auto;
      border-radius: 6px;
      box-shadow: 0 4px 9px 0 rgba(0, 0, 0, 0.13);
      width: 100%;
      max-width: 397px;

      @include mq_min_width($tablet-medium) {
        margin: 0 15px;
        max-width: none;
        width: calc((100% - 1 * 60px) / 2);
      }

      & > a {
        border-radius: 6px;
        position: relative;
        display: flex;
        height: 100%;
        flex-direction: column;
        text-decoration: none;
        overflow: hidden;

        @include mq_min_width($desktop-small) {
          min-height: 435px;
        }

        .at-illust,
        .gla-item-img,
        .la-item-img,
        .cx-lazy-load {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: 100%;
          height: 100%;
          z-index: 1;
          transition: transform 1s cubic-bezier(0.25, 0.46, 0.45, 0.94);
        }

        &:hover,
        &:focus,
        &:active {
          .at-illust,
          .gla-item-img,
          .la-item-img,
          .cx-lazy-load {
            transform: translate(-50%, -50%) scale(1.2);
            transition: transform 7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
          }
        }
      }
    }

    .icon {
      display: none;
    }
  }

  .an-item-publication {
    display: none;
  }
}
