.speakers-search {
  margin: 30px auto 15px;

  h2 {
    font-family: $font-2;
    font-size: 25px;
    font-weight: 900;
    line-height: 1.6;
    color: $textcolor-2;
    border: 0;
  }

  input {
    height: 58px;
    width: 100%;
    font-size: 16px;
    font-weight: 300;
    line-height: 1.8125;
    border-radius: 7px;
    background: none;
    color: $lightgrey-2;
    border: 0;
    padding: 0 20px;
  }

  button {
    background: none;
    border: 0;
    padding: 0 30px;
    color: $theme-color-2;
  }

  input,
  button {
    min-height: 58px;
  }

  &__group {
    display: flex;
    align-items: center;
    border-radius: 7px;
    background-color: rgba($lightgrey-5, 0.6);

    @include mq_min_width($desktop-small) {
      max-width: 65%;
    }
  }

  &__field {
    width: 100%;
  }
}