@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.list-articles.block.block-page.prix-inno {
  position: relative;
  width: 100%;
  margin: 40px 0;
  padding: 50px 0 40px;

  @include mq_min_width($tablet-medium) {
    margin: 80px 0;
  }

  @include mq_min_width($desktop-medium) {
    width: calc(100% - 107px);
    padding: 69px 77px 80px 0;
  }

  &::before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    right: 50%;
    width: 100vw;
    height: 100%;
    background: $theme-color-1;
    transform: translateX(50%);

    @include mq_min_width($desktop-medium) {
      right: 0;
      transform: none;
    }
  }

  .custom_generic_progressBar {
    @if not $is_mobile {
      display: none !important;
    } @else {
      position: relative;
      z-index: 2;
      bottom: auto;
      margin-top: 30px;
    }
  }

  .prix-inno__arrows-container,
  & > .inside {
    position: relative;
    z-index: 2;
  }

  .prix-inno__arrows-container {
    @include slider-arrows;
    display: none;

    @include mq_min_width($tablet-medium) {
      display: flex;
    }

    @include mq_min_width($desktop-small) {
      display: none;
    }
  }

  .main-title-with-link {
    margin-bottom: 43px;
    border: 0;

    .main-title {
      float: none;
      font-size: 30px;
      font-weight: 900;
      line-height: 44px;
      text-transform: none;
      color: $textcolor-3;

      @include mq_min_width($desktop-small) {
        font-size: 40px;
        line-height: 60px;
      }
    }
  }

  .grid-la-list {
    margin: 0;
    align-items: stretch;
    justify-content: flex-start;
    flex-wrap: nowrap;

    .gla-item {
      width: calc(100vw - 50px);
      max-width: 330px;
      margin: 0;
      border-radius: 5px;
      transition: opacity .2s ease-in-out;

      &:not(.swiper-slide-visible) {
        opacity: .4;

        @include mq_min_width($desktop-small) {
          opacity: 1;
        }
      }

      @include mq_min_width($desktop-small) {
        width: calc((100% - 2 * 15px) / 3);
        max-width: none;

        @include mq_min_width($desktop-medium) {
          width: calc((100% - 2 * 37px) / 3);
        }

        &:not(:first-of-type) {
          margin-left: 15px;

          @include mq_min_width($desktop-medium) {
            margin-left: 37px;
          }
        }
      }

      & > a {
        border-radius: 5px;
        position: relative;
      }

      .at-illust,
      .gla-item-img,
      .la-item-img,
      .cx-lazy-load {
        width: 100%;
        height: 184px;
      }
    }

    .gla-item-content {
      height: calc(100% - 184px);
      min-height: 0;
      padding: 22px 25px 20px;
      justify-content: flex-end;
      align-items: flex-start;

      & > * {
        position: relative;
        z-index: 1;
      }

      .gla-item-tag {
        z-index: 0;
        position: absolute;
        top: calc(184px + 11px);
        right: 10.5px;
        left: auto;
        width: 61px;
        height: 45px;
        padding: 0;
        font-size: 0;
        letter-spacing: 0;
        text-indent: -99999px;
        color: transparent;
        background: transparent center /cover no-repeat;
        overflow: hidden;
        user-select: none;
        pointer-events: none;

        @each $award, $image in $inno-awards {
          &.topic_grand_prix_#{$award} {
            background-image: image('visual/#{$image}');
          }
        }
      }

      .gla-item-title {
        margin-bottom: 8px;
        font-family: $font-2;
        font-weight: 900;
        color: $textcolor-2;

        & > span[class^='category-prix'] {
          display: block;
          margin-bottom: 14px;
          font-size: 12px;
          font-weight: 500;
          letter-spacing: 1.04px;
          line-height: 22px;
        }
      }

      .gla-item-desc {
        margin-top: auto;
        font-family: $font-1;
        font-size: 15px;
        font-weight: 500;
        line-height: 27px;
        color: $lightgrey-2;

        strong:not(:first-of-type) {
          &::before {
            content: '\A';
            white-space: pre;
          }
        }
      }

      .gla-item-btn {
        margin: 7px 0 0;

        .gla-btn-read-more {
          font-size: 0;
          letter-spacing: 0;
          color: transparent;

          &::before {
            font-size: 16px;
            letter-spacing: normal;
            color: $textcolor-5;

            html[lang='fr'] & {
              content: 'Voir plus';
            }

            html[lang='en'] & {
              content: 'See more';
            }
          }
        }
      }
    }
  }

  & + .block.block-page.prix-secteurs {
    &::after {
      @include mq_min_width($desktop-medium) {
        content: '';
        position: absolute;
        z-index: 0;
        top: -344px;
        left: 50%;
        width: 200vw;
        height: 799px;
        background: #353535;
        transform: translateX(-50%);
      }
    }
  }
}
